.navigation {
  position: fixed;
  left: 2%;
  top: 50%;
  transform: translateY(-50%);
  width: 70px;
  height: 230px;
  background: rgba(245, 245, 245, 0.161);
  border-radius: 35px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  opacity: 0;
  filter: blur(4px);
  animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);

  ul {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    flex-direction: column;

    li {
      width: 70px;
      position: relative;
      height: 70px;
      z-index: 1;
      a {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        text-align: center;
        color: rgb(1, 1, 1);
        font-weight: 500;
        .icon {
          position: relative;
          display: block;
          line-height: 75px;
          text-align: center;
          i {
            font-size: 24px;
          }
        }
        .title {
          position: absolute;
          top: 50%;
          left: 110px;
          background: #fff;
          transform: translateY(-50%);
          padding: 5px 10px;
          border-radius: 6px;
          transition: 0.5s;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}

.navigation ul li:hover a .title {
  opacity: 1;
  visibility: visible;
  transform: translateX(-25px) translateY(-50%);
}
.navigation ul li a .title::before {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background: #fff;
  left: -8px;
  top: 46%;
  transform: rotate(45deg) translateY(-50%);
  border-radius: 2px;
}
.navigation ul .indicator {
  position: absolute;
  left: 0;
  width: 70px;
  height: 70px;
  transition: 0.5s;
}
.navigation ul .indicator::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background: #333;
  border-radius: 50%;
  transition: 0.5s;
}
.navigation ul li.active a .icon {
  color: #fff;
}
.navigation ul li:nth-child(1).active ~ .indicator {
  transform: translateY(calc(70px * 0));
}
.navigation ul li:nth-child(2).active ~ .indicator {
  transform: translateY(calc(70px * 1));
}
.navigation ul li:nth-child(3).active ~ .indicator {
  transform: translateY(calc(70px * 2));
}
.navigation ul li:nth-child(4).active ~ .indicator {
  transform: translateY(calc(70px * 3));
}
.navigation ul li:nth-child(5).active ~ .indicator {
  transform: translateY(calc(70px * 4));
}
.navigation ul li:nth-child(1).active ~ .indicator::before {
  background: #f53b57;
}
.navigation ul li:nth-child(2).active ~ .indicator::before {
  background: rgb(44, 78, 231);
}
.navigation ul li:nth-child(3).active ~ .indicator::before {
  background: rgb(255, 145, 0);
}
