@import url("https://fonts.googleapis.com/css2?family=Nunito&display=swap");
.projects-container {
  background: #000;
  background-blend-mode: darken;
  color: whitesmoke;
  opacity: 0.8;
  filter: blur(4px);
  animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
  h2 {
    font-weight: 200;
    letter-spacing: 2px;
    font-size: 3em;
    text-align: center;
    @media screen and(max-width:580px) {
      font-size: 2em;
    }
  }
  .react-player video {
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
      transform: scale(0.99);
    }
  }

  p {
    width: 80%;
    text-align: center;
    font-size: 0.8em;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: "Nunito", sans-serif;
    user-select: none;
    cursor: pointer;
    @media screen and(max-width:540px) {
      font-size: 0.5em;
    }
    @media screen and(max-height:400px) {
      font-size: 0.3em;
    }
  }
  .section-1 {
    padding: 20px;
    position: relative;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100vh;
    width: 100%;
    background: url(../../../public/images/sand.jpg) center/cover;
  }
  .section-2 {
    padding: 20px;
    position: relative;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100vh;
    width: 100%;
    background: url(../../../public/images/crypto.png) center/cover;
    h2 {
      color: #000;
    }
    p {
      padding-top: 30px;
      color: #000;
      font-weight: 700;
    }
  }
  .section-3 {
    padding: 20px;
    position: relative;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
    width: 100%;
    background: url(../../../public/images/resto.jpg) center/cover;
  }
}
