.profile-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5) url("../../../public/images/profile.jpg")
    center/cover;
  background-blend-mode: darken;
  overflow: hidden;
  .grid-container {
    min-height: 650px;
    width: 80%;
    height: 80%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-box-shadow: 2px 4px 16px 8px rgba(0, 0, 0, 0.79);
    box-shadow: 2px 4px 36px 18px rgba(0, 0, 0, 0.79);
    opacity: 0;
    filter: blur(4px);
    animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
    @media screen and(max-width: 1000px) {
      grid-template-columns: none;
      grid-template-rows: 1fr 1fr;
    }
    .left {
      text-align: center;
      padding: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      color: whitesmoke;
      @media screen and(max-width: 1000px) {
        padding: 0;
      }
      h2 {
        margin-top: 50px;
      }
      p {
        font-size: 1.2em;
        letter-spacing: 1px;
        line-height: 25px;
        @media screen and(max-width: 1240px) {
          font-size: 0.8em;
        }
        @media screen and(max-width: 1000px) {
          padding: 0 30px;
        }
      }
      span {
        text-transform: uppercase;
        color: aqua;
      }
      img {
        margin: 120px 0 60px 0;
        width: 180px;
        height: 180px;
        border-radius: 10px;
        -webkit-box-shadow: 2px 4px 16px 8px rgba(0, 0, 0, 0.79);
        box-shadow: 2px 4px 16px 8px rgba(0, 0, 0, 0.79);
      }
      a {
        text-decoration: none;
        color: whitesmoke;
        margin-top: 40px;
        font-size: 3em;
        i {
          opacity: 0.5;
          transition: 0.3s ease-in-out;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      justify-content: center;
      .container {
        position: relative;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 40px;
        @media screen and(max-width: 1240px) {
          gap: 0;
        }
        .card:nth-child(1) {
          position: relative;
          width: 220px;
          height: 250px;
          display: flex;
          align-items: center;
          justify-content: center;
          @media screen and(max-width: 540px) {
            width: 150px;
            height: 150px;
          }
          .percent {
            position: relative;
            width: 150px;
            height: 150px;
            svg {
              position: relative;
              width: 150px;
              height: 150px;
              transform: rotate(270deg);
              circle {
                width: 100%;
                height: 100%;
                fill: transparent;
                stroke-width: 2;
                stroke: #191919;
                transform: translate(5px, 5px);
                &:nth-child(2) {
                  stroke: rgb(52, 220, 29);
                  stroke-dasharray: 440;
                  stroke-dashoffset: calc(440 - (440 * 95) / 100);
                  animation: fadeIn 1s linear forwards;
                  opacity: 0;
                  animation-delay: 1s;
                }
              }
            }
            .dot {
              position: absolute;
              inset: 5px;
              z-index: 10;
              animation: animateDot1 1s linear forwards;
              &::before {
                content: "";
                position: absolute;
                width: 10px;
                top: -5px;
                left: 50%;
                transform: translateX(-50%);
                height: 10px;
                border-radius: 50%;
                background: rgb(52, 220, 29);
                box-shadow: 0 0 10px rgb(52, 220, 29);
              }
            }

            .number {
              position: absolute;
              inset: 0;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              animation: fadeIn 1s linear forwards;
              opacity: 0;
              animation-delay: 1s;
              h2 {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #fff;
                font-weight: 700;
                font-size: 2em;
                span {
                  font-weight: 300;
                  font-size: 0.5em;
                }
              }
              p {
                font-weight: 300;
                color: #fff;
                font-size: 0.75em;
                letter-spacing: 2px;
                text-transform: uppercase;
                color: rgba(255, 255, 255, 0.751);
              }
            }
          }
        }
