.home {
  background: rgba(0, 0, 0, 0.5) url("../../../public/images/home-bg.jpg")
    center/cover;
  background-blend-mode: darken;
  min-height: 100vh;
  overflow: hidden;
  .section-1 {
    color: whitesmoke;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h1 {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      font-size: 7em;
      @media screen and(max-width:670px) {
        font-size: 5em;
      }
      @media screen and(max-width:540px) {
        font-size: 3em;
      }
      @media screen and(max-width:420px) {
        font-size: 2em;
      }
    }
    span {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);

      font-size: 3em;
      @media screen and(max-width:540px) {
        font-size: 2em;
      }
      @media screen and(max-width:420px) {
        font-size: 1em;
      }
    }
  }
}
@keyframes animate {
  100% {
    transform: scale(1.1);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
